import { useEffect, useRef } from 'react';
import { useMathJax } from './MathJaxProvider';
import { cleanMathContent } from './mathjax-utils';

export default function MathJaxRenderer({ content, display = true }) {
  const ref = useRef(null);
  const mathJax = useMathJax();

  useEffect(() => {
    if (!mathJax || !ref.current || !content) return;

    try {
      const { adaptor, html } = mathJax;
      const cleanedContent = cleanMathContent(content);
      
      // Clear previous content
      ref.current.innerHTML = '';
      
      // Create container with math content
      const container = adaptor.node('div', {}, [
        adaptor.node('script', { type: 'math/tex' }, [cleanedContent])
      ]);
      
      // Convert math content
      const node = html.convert(container, {
        display,
        em: 16,
        ex: 8,
        containerWidth: 80 * 16
      });
      
      // Append to DOM
      ref.current.appendChild(adaptor.domNode(node));
      
    } catch (err) {
      console.error('MathJax rendering error:', err);
      ref.current.innerHTML = content; // Fallback to original content
    }
  }, [content, mathJax, display]);

  return <div ref={ref} className="mathjax-content" />;
}