import { createContext, useContext, useEffect, useState } from 'react';
import { mathjax } from 'mathjax-full/js/mathjax';
import { TeX } from 'mathjax-full/js/input/tex';
import { CHTML } from 'mathjax-full/js/output/chtml';
import { liteAdaptor } from 'mathjax-full/js/adaptors/liteAdaptor';
import { RegisterHTMLHandler } from 'mathjax-full/js/handlers/html';

const MathJaxContext = createContext(null);

export function MathJaxProvider({ children }) {
  const [mathJax, setMathJax] = useState(null);

  useEffect(() => {
    // Initialize MathJax components
    const adaptor = liteAdaptor();
    RegisterHTMLHandler(adaptor);

    // Configure TeX input
    const tex = new TeX({
      packages: ['base', 'ams', 'newcommand', 'physics'],
      inlineMath: [['$', '$'], ['\\(', '\\)']],
      displayMath: [['$$', '$$'], ['\\[', '\\]']],
      processEnvironments: true,
      processEscapes: true,
      macros: {
        '\\matrix': '\\begin{matrix}',
        '\\endmatrix': '\\end{matrix}',
        '\\pmatrix': '\\begin{pmatrix}',
        '\\endpmatrix': '\\end{pmatrix}',
        '\\bmatrix': '\\begin{bmatrix}',
        '\\endbmatrix': '\\end{bmatrix}'
      }
    });

    // Configure CHTML output
    const chtml = new CHTML({
      fontURL: 'https://cdn.jsdelivr.net/npm/mathjax@3/es5/output/chtml/fonts/woff-v2',
      adaptiveCSS: true
    });

    // Create document processor
    const html = mathjax.document('', {
      InputJax: tex,
      OutputJax: chtml
    });

    setMathJax({ adaptor, tex, chtml, html });
  }, []);

  return (
    <MathJaxContext.Provider value={mathJax}>
      {children}
    </MathJaxContext.Provider>
  );
}

export function useMathJax() {
  return useContext(MathJaxContext);
}