// utils/mathjax-utils.js
export const cleanMathContent = (html) => {
  if (!html) return '';
  
  // Preserve math content while cleaning HTML
  return html
    .replace(/<p>/g, ' ')      // Replace paragraph tags
    .replace(/<\/p>/g, ' ')     // with spaces
    .replace(/<br\s?\/?>/g, ' ') // Handle line breaks
    .replace(/<[^>]*>/g, ' ')   // Remove all other HTML tags
    .replace(/\s+/g, ' ')       // Collapse multiple spaces
    .trim();
};

export const extractMath = (content) => {
  // Match $...$, \(...\), \[...\], and \begin...\end environments
  const mathRegex = /(\$.*?\$|\\\(.*?\\\)|\\\[.*?\\\]|\\begin\{.*?\}.*?\\end\{.*?\})/gs;
  return content.match(mathRegex) || [];
};